const stories=[
{"story_id":0,"story":"Подумай о том, что волнует тебя и спроси об этом. Здесь ты найдешь ответ."},{"story_id":1,"story":"Самые счастливые, самые удачливые люди однажды задумывались о самоубийстве. И отвергли его."},{"story_id":2,"story":"Ты свободен творить и почитать любое прошлое, как ты выберешь, чтобы исцелить и преобразить собственное настоящее."},{"story_id":3,"story":"Твоя самая суровая действительность – это только сон, а твои самые фантастические мечты – реальность."},{"story_id":4,"story":"Каждая вещь есть в точности то, что она есть, по какой-то причине. Крошка на твоем столе – это не мистическое напоминание об утреннем печенье; она лежит там потому, что твой выбор – не убирать ее. Исключений нет."},{"story_id":5,"story":"Не думай, что тот, кто свалился на тебя из иного измерения, хоть в чем нибудь мудрее тебя. Или он что-то сделает лучше, чем мог бы ты сам. Бесплотен человек или смертен, в людях важно одно: что они знают."},{"story_id":6,"story":"Каждый приходит сюда с ящиком инструментов и комплектом проектной документации на построение Собственного Будущего. Вот только не каждый помнит, куда он все это засунул."},{"story_id":7,"story":"Жизнь не говорит тебе ничего, она все показывает."},{"story_id":8,"story":"Ты узнал нечто такое, что кому-то где-то необходимо вспомнить. Как ты сообщишь им свое знание?"},{"story_id":9,"story":"Прими свои страхи, позволь им вытворять самое худшее – и отсеки их, когда они попробуют этим воспользоваться. Если ты этого не сделаешь – они начнут сами себя клонировать, как грибы, окружат тебя со всех сторон и закроют дорогу к той жизни, которую ты хочешь избрать. Каждый поворот, которого ты боишься, - лишь пустота, которая прикидывается неодолимой преисподней."},{"story_id":10,"story":"Снова и снова ты будешь встречать новую теологию, и всякий раз проверяй ее: – Хочу ли я, чтобы это верование вошло в мою жизнь?"},{"story_id":11,"story":"Если бы Бог посмотрел тебе прямо в глаза и сказал: – Повелеваю, чтобы ты был счастлив в этом мире, доколе жив. Что бы ты сделал?"},{"story_id":12,"story":"Это называется “принимать на веру”; когда ты соглашаешься с правилами прежде, чем обдумаешь их, или когда совершаешь действия потому, что от тебя их ожидают. Если ты неосторожен, это будет случаться тысячи и тысячи раз в продолжении всей твоей жизни."},{"story_id":13,"story":"Что, если все эти твои внутренние уровни – на самом деле твои друзья, знающие неизмеримо больше, чем знаешь ты? Что, если твои учителя находятся здесь прямо сейчас? И чем говорить без умолку, не лучше ли тебе – для разнообразия – послушать?"},{"story_id":14,"story":"Жизнь не требует, чтобы ты был последовательным, жестоким, терпеливым, внимательным, сердитым, рациональным, бездумным, любящим, стремительным, восприимчивым, нервным, заботливым, черствым, толерантным, расточительным, богатым, подавленным, вежливым, пресыщенным, тактичным, веселым, тупым, здоровым, жадным, красивым, ленивым, отзывчивым, глуповатым, щедрым, загнанным, чувственным, сластолюбивым, прилежным, манипулятивным, управляемым, прозорливым, капризным, мудрым, эгоистичным, добрым или жертвенным. Однако Жизнь требует, чтобы ты осознавал последствия каждого своего выбора."},{"story_id":15,"story":"Гнев – всегда страх, а страх – всегда страх потери."},{"story_id":16,"story":"Помни, что этот мир – не реальность. Это площадка для игры в кажущееся. Здесь ты практикуешься побеждать кажущееся знанием истинного."},{"story_id":17,"story":"Хочешь беззаботного будущего? Но зачем тогда было соваться в пространство-время, если ты избегаешь забот?"},{"story_id":18,"story":"Чем меньше ты знаешь об игре, чем больше забываешь, что ты – игрок, тем бессмысленнее становится жизнь."},{"story_id":19,"story":"Если вдохновенный невинный любящий витающий в облаках мечтатель верует во Вселенную радости, и света, и совершенного бытия и если он ошибается и умирает, то заблуждается не он, а Вселенная."},{"story_id":20,"story":"То, что происходит вокруг тебя, выбираешь ты сам. Держись, проживи свою жизнь наилучшим известным тебе образом и вскоре ты поймешь, в чем тут дело."},{"story_id":21,"story":"Подлинное государство – это такая система ценностей, для которой «патриотизм» и «совесть» – понятия тождественные."},{"story_id":22,"story":"На каждом повороте твоей жизни, всякий раз, когда ты принимаешь решение, рождается новая часть тебя. Ты становишься родителем твоих новых «Я», которые придут."},{"story_id":23,"story":"Любая могучая идея абсолютно восхитительна и в высшей степени бесполезна, пока ты не решишься заставить ее работать."},{"story_id":24,"story":"Когда ты ищешь вдохновения, на самом деле тебе нужны идеи. Если ты просишь наставлений, - именно идеи укажут тебе путь. Но будь внимателен!"},{"story_id":25,"story":"Делай вид, что ты честно, воистину всей душей хочешь знать, кто ты, откуда пришел и зачем. Притворяйся, что ты готов не знать покоя, пока не выяснишь это. А теперь: Можешь ли ты представить себе, что не ищешь ответа?"},{"story_id":26,"story":"Плохо – это то, что делает тебя несчастливым. «Зло» – это то, что делает тебя очень несчастливым."},{"story_id":27,"story":"Твоя философия – это твои представления о Вселенной, которыми ты руководствуешься в повседневной жизни. Вряд ли их можно найти в учебниках."},{"story_id":28,"story":"Ты не получаешь ответов? Самая главная причина в том, что ты не задаешь вопросов."},{"story_id":29,"story":"Когда, ухватившись за лопасть пропеллера, начинаешь прокручивать его, не удивляйся, если вдруг заработает двигатель."},{"story_id":30,"story":"Все, что ты решаешь пережить, переживешь не раз, а тысячи раз, пока не запомнишь этот опыт до конца своей жизни."},{"story_id":31,"story":"Чувство вины – это напряжение, которое ты испытываешь, когда хочешь изменить свое прошлое, настоящее или будущее ради кого-то. Но ведь это твое напряжение. Ты можешь отпустить его."},{"story_id":32,"story":"Ничего ты не знаешь – пока интуиция не даст своего согласия."},{"story_id":33,"story":"Когда спишь, все сценарии, персонажи, события, опасности и развязки строятся из материалов твоего сознания – все темные и гнетущие состояния, равно как и все восторги. То же самое и в мире пробуждения, только на строительство уходит гораздо больше времени."},{"story_id":34,"story":"Сторгуй себе безопасность, предлагая взамен счастье, - за эту сцену ее можно купить."},{"story_id":35,"story":"Многие ли проживают жизнь, не пытаясь узнать, что они знают и что любят? Многие. Твое дело – ни в коем случае не попасть в их число."},{"story_id":36,"story":"Когда корабль твоего духа наткнется на риф материи, то на куски расколется риф."},{"story_id":37,"story":"Если это действительно не твоя вина, ты не можешь брать на себя ответственность за происходящее. Если ты не можешь взять на себя ответственность за происходящее, то всегда будешь его жертвой."},{"story_id":38,"story":"Глубина твоей интимности с другим человеком обратно пропорциональна числу других людей в твоей жизни."},{"story_id":39,"story":"Человечности нельзя дать физическое описание, ибо это духовная цель. Она не может быть дана тебе – ее нужно заслужить."},{"story_id":40,"story":"Твое высшее право – знать будущее во всех его версиях. Прислушайся к его шепоту, и услышишь, что награда, ждущая тебя впереди, - твое собственное величайшее счастье."},{"story_id":41,"story":"Действовать обыденно – это нормально, при условии, что ты не чувствуешь обыденно."},{"story_id":42,"story":"Есть вершины, взобравшись на которые ты больше не спускаешься вниз, а, расправив крылья, летишь ввысь."},{"story_id":43,"story":"То, что должно случиться, уже случалось. То, как ты воспринимаешь, - это твой выбор."},{"story_id":44,"story":"Ты никогда не повзрослеешь."},{"story_id":45,"story":"Почему ты думаешь, что ты иной и странный, одинокий, никем не понятый? Если причина лишь в том, что ты еще не встретил свою пару…"},{"story_id":46,"story":"При каждом своем выборе ты рискуешь жизнью, которая у тебя могла бы быть; при каждом решении ты теряешь ее."},{"story_id":47,"story":"Ты строишь собственный мир, спокойный или тревожный, в соответствии с тем, чего ты хочешь. Можешь обрести покой среди хаоса, можешь быть несчастным в раю. Все это лишь форма воплощения твоего духа."},{"story_id":48,"story":"Найди величайших учителей, задай им труднейшие вопросы – они никогда не скажут: «Изучай филосояию» или «Защити диссертацию». Они скажут: «Ты уже знаешь»."},{"story_id":49,"story":"Это очень долгое дело – менять принципы, и никогда не знаешь, изменились ли они уже, пока не почувствуешь, что что-то устоявшееся и правильное перестало быть таковым."},{"story_id":50,"story":"Безусловная любовь – не более реальная сила в пространстве-времени, чем в шахматах, или в футболе, или в хоккее. Правила регламентируют жизнь в игре, а безусловная любовь не знает правил."},{"story_id":51,"story":"Забвение это то, что люди на этой планете называют «сознанием»."},{"story_id":52,"story":"Еле заметный поворот сегодня приведет тебя к драматически иному завтра."},{"story_id":53,"story":"То, что ты знал до своего рождения, не потеряно. Ты только прячешь это до первых испытаний, когда придет время вспомнить. И конечно же, если захочешь, ты можешь попытаться ступить на какой-нибудь странный, забавный и прекрасный путь и найти этот клад."},{"story_id":54,"story":"Ты выбрал нас В качестве своих учителей? Мы тоже выбрали тебя! Тебя интересует, чему ты будешь учиться? Нас это тоже интересует! Ты думаешь, мы появились в твоей жизни потому, что ты любишь нас? Ну как же ты не поймешь: мы тоже любим тебя!"},{"story_id":55,"story":"Могут ли на самом деле километры разделить людей? Если ты хочешь быть с тем, кого любишь, разве вы уже не вместе?"},{"story_id":56,"story":"Забудь о вере. Тебе не нужна вера, чтобы летать, тебе нужно понять полет."},{"story_id":57,"story":"Когда ты постигнешь, что такое этот мир и как он работает, у тебя сами собой начнут получаться чудеса… То, что другие люди будут называть чудесами."},{"story_id":58,"story":"Вы забавные игривые создания, играющие «в жизнь» со Вселенной."},{"story_id":59,"story":"Пространство-время – довольно примитивная школа. Однако многие продолжают веровать в нее, даже когда она начинает надоедать, и еще долго не хотят включать сигнальные огни."},{"story_id":60,"story":"Подобное притягивает подобное. Будь самим собой – спокойным, ясным, ярким. Спрашивай себя каждую минуту, то ли это, чего ты действительно хочешь, и делай это, только когда услышишь утвердительный ответ. Это отвадит от тебя тех, с которыми ты ничего о себе не узнаешь, и привлечет других, у которых тебе стоит поучиться."},{"story_id":61,"story":"«Это только твоя фантазия?» Конечно, это твоя фантазия. Весь этот мир – твоя фантазия. Ты что, забыл?"},{"story_id":62,"story":"Ты не для того существуешь, чтобы оставить в мире след. Ты существуешь, чтобы прожить свою жизнь, К тому же прожить ее так, Чтобы быть счастливым."},{"story_id":63,"story":"Всякое другое пространство-время – это мечта для всякого нормального, здорового землянина, которым ты собираешься быть – правда, совсем не долго."},{"story_id":64,"story":"Сколько жизней на этой планете? Сколько жизней во Вселенной? – Единственная."},{"story_id":65,"story":"Великие награды ждут тех, кто выбирает благородный и трудный путь, но эти награды становятся явными лишь по прошествии лет. И каждый такой выбор делается на ощупь, без всяких гарантий от мира, который тебя окружает."},{"story_id":66,"story":"Как бы ты ни был искусен и достоин, тебе не достичь лучшей жизни, пока ты ее, во-первых, – не представишь и, во-вторых, – не позволишь ее себе."},{"story_id":67,"story":"Создавая свой собственный мир, ты получаешь в нем то, чего достоин. Кого винить, кого восхвалять, если не тебя, создателя этого мира? Кто способен изменить это в любую минуту, если не ты сам?"},{"story_id":68,"story":"Твое единственное обязательство на протяжении всей твоей жизни – быть самим собой."},{"story_id":69,"story":"Это мое чувство высшей справедливости? Так ли это? Это то направление, куда меня влечет больше всего? Так ли это? На этом пути я смогу отдать миру лучшее, что во мне есть? Так ли это?"},{"story_id":70,"story":"Не думайте, что вас лишь маленькая горстка, созданий света, разбросанных по Земле, а все остальные – куски глины. Вы все – световые существа."},{"story_id":71,"story":"Реальность не имеет ничего общего с тем, что ты видишь, с твоим узким полем зрения. Реальность – это проявленная любовь, совершенная чистая любовь, не причесанная пространством и временем."},{"story_id":72,"story":"Поскольку ты един с той личностью, которой был секунду или неделю назад, поскольку ты един с той личностью, которой станешь через секунду или неделю, поскольку ты един с той личностью, с которой был жизнь назад, ты – тот самый, кто живет в иной жизни, ты – тот самый, кем будешь в сотнях жизней, которые считаешь своим будущим."},{"story_id":73,"story":"Возможный выбор своего участия в каком-нибудь настоящем приключении можно оценить следующим образом. Подумай: «Когда я потом оглянусь назад, я буду радоваться, что дерзнул или что воздержался?»"},{"story_id":74,"story":"Если любишь кого-то безусловно, тебе не важно кто он такой и чем занимается. Безусловная любовь внешне выглядит точно так же, как и безразличие."},{"story_id":75,"story":"Ненависть – это любовь со знаком минус. Так стоит ли питать ее ложью, разделяя и разрушая себя, если истина в том, что вы – одно?"},{"story_id":76,"story":"Ты волен быть самим собой, быть своим истинным Я здесь и сейчас, и ничто не может тебе помешать."},{"story_id":77,"story":"Ни один человек не в состоянии сделать что-либо не свойственное тому, кто он есть."},{"story_id":78,"story":"Есть хорошая забава: зажмурься и в наступившей темноте скажи себе: «Я – волшебник, и сейчас, открыв глаза, увижу созданный мною мир, за который я – и только я – несу полную ответсвтенность». А теперь медленно поднимай веки, как поднимают занавес над сценой. И конечно же, вот он, твой мир, в точности такой, каким ты его построил."},{"story_id":79,"story":"Если ты жаждешь истины и света, непременно найдешь их."},{"story_id":80,"story":"Когда оглядываешься на прожитые дни, кажется, что они пролетели за один миг. Время не медлит, и никто здесь надолго не задерживается."},{"story_id":81,"story":"Единственная вещь, с помощью которой можно убить мечту, – компромисс."},{"story_id":82,"story":"Как легко испытывать сочувствие, когда в беде ты видишь себя самого!"},{"story_id":83,"story":"Старайся понимать, и тогда – ты даже не заметишь этого – всего за несколько десятилетий у тебя сформируется система мышления, отвечающая на все твои вопросы."},{"story_id":84,"story":"Ты – выдумка для тех «я», которые выбирают другие пути и решения. Они – выдумка для тебя. Вам есть чему поучиться друг у друга."},{"story_id":85,"story":"Если хочешь чему то научиться, откажись от безопасности неведения."},{"story_id":86,"story":"Всю жизнь ожидаешь встречи с Кем-то, кто поймет тебя и примет таким, какой ты есть. А в самом конце обнаруживаешь, что этот Кто-то всегда с тобой. И это – ты сам."},{"story_id":87,"story":"Как ты хочешь избавиться от этого опыта? И как ты хочешь благодаря ему измениться?"},{"story_id":88,"story":"Никто не заставит тебя учиться. Учиться ты будешь тогда, когда захочешь этого."},{"story_id":89,"story":"Птенец прыгает потому, что доверяет своим крыльям, а лемминг – потому что так делают все. У первого это прорыв в новое измерение, у второго – самоубийство."},{"story_id":90,"story":"Стоит ли напрягаться ради того, чтобы прожить несчастливую жизнь?"},{"story_id":91,"story":"Индивидуальность – всегда исключение. И поэтому то, что «никому не под силу», может сделать каждый."},{"story_id":92,"story":"Легко пережить то, чего ожидаешь и к чему давно привык. Только когда в твою жизнь приходит неожиданное, она становится увлекательной."},{"story_id":93,"story":"То, что многие считают здравым смыслом, на самом деле – подавление их глубинного знания. Зачем такой здравый смысл?"},{"story_id":94,"story":"Прежде чем ты изменишься, что-то невероятно для тебя важное должно оказаться под угрозой."},{"story_id":95,"story":"Если ты не хочешь быть учителем, тебе лучше убраться с этой планеты."},{"story_id":96,"story":"Творческий ум рождает необычные связи. Как, впрочем, и ум сумасшедшего. Необычные связи творческого ума суть озарение."},{"story_id":97,"story":"Вы строите ваши жизни, как пауки ткут свою паутину. Иногда нужно множество попыток, чтобы соткать одну-единственную нить."},{"story_id":98,"story":"Всякое событие субъективно: дело не в том, что оно означает; дело в том, что оно значит для тебя."},{"story_id":99,"story":"Время заботится о том, чтобы стать. Реальность заботится о том, чтобы быть."},{"story_id":100,"story":"Нечего прощать, когда соберешь воедино все опыты своей жизни. Какой смысл сердиться на тех, кто дал тебе все, о чем ты просил?"},{"story_id":101,"story":"Очень многое можно сказать о человеке, если знаешь, что именно приносит ему утешение."},{"story_id":102,"story":"Самое действенное обучение – когда играешь против соперника, который может победить тебя."},{"story_id":103,"story":"Надежда знает: то, что прекрасно, – истинно, прежде, чем оно покажется на ее пороге."},{"story_id":104,"story":"Ты мастер в том, что пережил, ремесленник в том, что переживаешь, и дилетант в том, что тебе предстоит пережить."},{"story_id":105,"story":"Найди свою силу и станешь тем центром, вокруг которого вращается само твое время."},{"story_id":106,"story":"Если ты живешь по высшим законам своей души, результат игры не имеет значения. Чем бы она ни закончилась, она закончится правильно."},{"story_id":107,"story":"Даже если вы встретили друг друга по законам божественного притяжения, вам придется всерьез поработать над весьма непростыми задачами."},{"story_id":108,"story":"Время жизни – это шанс выразить твое пребывание здесь самым рискованным, самым творческим способом, который доступен твоему воображению."},{"story_id":109,"story":"Признай, что твой инстинкт самосохранения – это единственная реальность, в которой ты можешь быть уверен."},{"story_id":110,"story":"Ты настраиваешь свое восприятие на определенную частоту, и все, что в результате видишь, именуешь «этим миром». А ведь ты можешь настраивать себя и на другие частоты. Всякий раз, когда пожелаешь."},{"story_id":111,"story":"Творческая, озаренная любовью жизнь – это также и жизнь здоровая. Если ты делаешь то, что приносит тебе счастье, ты тем самым исцеляешь и защищаешь себя."},{"story_id":112,"story":"Первый шаг к выходу из гипноза: прежде всего необходимо понять, что ты загипнотизирован."},{"story_id":113,"story":"Никакого рая, никакого ада, только эти бесконечные миры, которые ты создаешь, пока думаешь, что так и надо."},{"story_id":114,"story":"Сертификат Мастера Настоящим удостоверяется, что предъявителю этого документа присуждено звание Мастера Пространства-Времени с предоставлением права абсолютного контроля над всеми событиями собственной жизни и одновременно над бесконечным количеством жизненных опытов, а также права свободно уделять им внимание, свободно, по собственному желанию, выбирать торжество или трагедию для собственного обучения или развлечения. Настоящий сертификат подлежит следующим ограничениям: налагаются самим владельцем ______________________________ ______________________________ Он здесь, этот сертификат, в кармане у каждого."},{"story_id":115,"story":"Нет ничего, с чем ты не мог бы согласиться. Нет ничего, от чего ты не мог бы отказаться."},{"story_id":116,"story":"Гарантии на трудную и счастливую жизнь: 1. Определи, что ты любишь делать больше всего на свете. 2. Делай это, что бы ни стояло на твоем пути. 3. То, чему ты научился у этой любви, дари другим, тем, кто понимает ценность этих даров и просит о них."},{"story_id":117,"story":"Это реально? Да, если: это всегда было, это всегда будет и это всегда есть внутри и вне каждого уголка каждой альтернативной вселенной."},{"story_id":118,"story":"Если ты хочешь встретить того, кто способен уладить любую ситуацию, которая тебе не нравится, кто может дать тебе счастье, что бы там ни говорили и что бы ни думали другие, - посмотри в зеркало и скажи волшебное слово: –Привет!"},{"story_id":119,"story":"Когда ты делишься своей любовью, тебя ведут к волшебной жизни и глубокому счастью. Те, кто не делится, понятия об этом не имеют."},{"story_id":120,"story":"Когда отдаешь скупо, скупо и получишь. Когда отдаешь целые миры, Немыслимо огромной будет и твоя награда."},{"story_id":121,"story":"Твоя высшая истина знает все будущие. Прислушайся к ее шепоту, и ты услышишь, что самый большой приз – твое величайшее счастье."},{"story_id":122,"story":"Узнай то, что знает волшебник, и волшебство исчезнет."},{"story_id":123,"story":"Ты можешь не осознавать, но ты знаешь."},{"story_id":124,"story":"Прежде, чем причинить вред другому, причини его самому себе."},{"story_id":125,"story":"Поживи вволю тем, что ты всегда мечтал делать, и не останется места для плохого самочувствия."},{"story_id":126,"story":"Не проси у Бога внимания в надежде, что он уладит какую-то проблему. Помолись о собственном внимании, и поймешь, что нет никакой проблемы и ничего не нужно улаживать."},{"story_id":127,"story":"Твоя миссия – сияющий путь, которым ты следуешь, и не важно, насколько темна ночь вокруг."},{"story_id":128,"story":"На любой вопрос вообрази самую крайнюю противоположность ожидаемому ответу. Может быть, это и есть правильный ответ?"},{"story_id":129,"story":"Одни говорят: страдай, другие говорят: служи, третьи говорят: отрешись. Говорит ли кто-нибудь: найди для себя высшую истину?"},{"story_id":130,"story":"Здесь нет ничего, кроме совершенства. Совершенное выражение совершенной жизни: «Изменению не подлежит». Нет иной истины."},{"story_id":131,"story":"Смерть тела, расставание с телом – помни! – такой же сон, как и жизнь в нем."},{"story_id":132,"story":"Учиться – значит обнаруживать то, что ты уже знаешь. Делать – значит демонстрировать то, что ты это знаешь. Учить других – значит напоминать им, что они знают – точно так же, как и ты. Все вы учитесь, делаете, учите."},{"story_id":133,"story":"Незнание истины не мешает ей быть истиной."},{"story_id":134,"story":"Ты волен улыбаться в самый разгар самых трудных экзаменов и испытаний, зная, что это твой выбор, что ты сам захотел играть в эту игру, что ты – властелин всех иллюзий этого мира."},{"story_id":135,"story":"Простейшие вопросы – самые глубокие. Где ты родился? Где твой дом? Куда ты идешь? Что ты делаешь? Думай об этом время от времени и следи за ответами – они изменяются."},{"story_id":136,"story":"Жизнь смертного – прекрасное место для посещения, но лучше не называть его домом."},{"story_id":137,"story":"Каждый человек и каждое событие в твоей жизни появляются потому, что ты сам призвал их туда. Что ты выберешь делать с ними – решать тебе."},{"story_id":138,"story":"То, что можно разделить на атомы,- нереально."},{"story_id":139,"story":"Лучше всего ты обучаешь тому, чему больше всего хочешь научиться сам."},{"story_id":140,"story":"Тебе не стоит стыдиться за свою жизнь, если то, что ты сказал или сделал, стало известно всему миру,- даже если то, что стало известно, не соответствует действительности."},{"story_id":141,"story":"Твои друзья знают тебя лучше в первую минуту вашей встречи, чем твои знакомые узнают тебя за тысячу лет."},{"story_id":142,"story":"Быть — это Жизнь, Любовь, это Великолепное Оно в самой сердцевине твоего существа. Быть не признает ограничений пространства-времени, не признает твоих горестей, страхов и верований. Оно не видит тебя как прямоходящего двуногого на поверхности третьей от маленького солнца планеты на краю небольшой галактики в незначительной вселенной, затерянной среди множества триллионов других вселенных. Оно видит в тебе отражение Себя и позволяет тебе абсолютную свободу делать все что угодно, кроме одного – умереть."},{"story_id":143,"story":"Если хочешь, чтобы в твоей жизни что-то появилось, вообрази, что оно уже здесь."},{"story_id":144,"story":"Бедствие – это сигнал к изменению. Это возможность в свободной одежде с капюшоном."},{"story_id":145,"story":"Лучший способ избежать ответственности – сказать: «Мне уже есть за что отвечать»."},{"story_id":146,"story":"Прислушивайся к своим ответам, даже когда они совершенно бездумны, при условии, что в них – твоя высочайшая правда."},{"story_id":147,"story":"Ты волен изменить свой уровень сознания – по прихоти, по замыслу или оттого, что стал умнее. Ты не волен сдержать проявления жизни."},{"story_id":148,"story":"Связь, которая соединяет твою истинную семью,- это не кровные узы, а уважение и радость, которые вы вносите в жизнь друг друга. Члены одной семьи редко вырастают под одной и той же крышей."},{"story_id":149,"story":"Пo жизненному пути тебя ведет любопытное внутреннее создание, игривое духовное существо — твое настоящее Я. Не отворачивайся от возможных вариантов будущего, не убедившись сначала, что тебе там нечему учиться. Ты всегда волен передумать и выбрать иное будущее или иное прошлое."},{"story_id":150,"story":"Защищай свои несовершенства. Имеешь право. Ведь они – твои собственные."},{"story_id":151,"story":"Легко позабыть те времена, когда ты знал, решив, что это был сон или однажды случившееся давнее чудо. Но ничто хорошее не бывает чудом, а прекрасное – грезой."},{"story_id":152,"story":"Не верь тому, что говорят тебе твои глаза: все, что они тебе показывают, - ограничения. Смотри собственным пониманием, открывай то, что уже знаешь, и увидишь путь, где ты умеешь летать."},{"story_id":153,"story":"Нет такого бедствия, которое не может стать благословением, и нет благословения, которое не может обернуться бедствием."},{"story_id":154,"story":"Весь мир – это твоя тетрадка для упражнений, на страницах которой ты учишься складывать. Это не реальность, хотя реальность ты можешь там выразить, если захочешь. И ничто не мешает тебе записывать там всякую чепуху, или ложь, или вырывать страницы."},{"story_id":155,"story":"Слушай свою жизнь. Она показывает все, что тебе необходимо знать насчет того, кем ты можешь быть."},{"story_id":156,"story":"Первородный грех – это ограничение Бытия. Не сделай этого."},{"story_id":157,"story":"Твой характер развивается оттого, что ты следуешь своему высшему чувству правоты, что ты полагаешься на идеалы, хотя и не уверен, что они работают."},{"story_id":158,"story":"Не существует такой вещи, как проблема, в руках которой нет подарка для тебя. Ты ищешь проблемы, потому, что нуждаешься в их дарах."},{"story_id":159,"story":"Одно из самых трудных испытаний на земле заключается в том, чтобы подняться выше мертвых систем – войн, религий, наций, разрушений, — отказаться быть их частью, а вместо этого выразить свое высшее Я. Ты знаешь, как быть."},{"story_id":160,"story":"Облако не знает, почему оно движется в этом направлении и с такой скоростью. Оно чувствует побуждение: «Вот сейчас нужно лететь туда». Но небо знает причины и траектории, по которым движутся все облака, и ты тоже будешь знать, когда поднимешься достаточно высоко, чтобы заглянуть за горизонт."},{"story_id":161,"story":"Если ты однажды научишься быть выдумкой, то поймешь, что выдуманные характеры иногда оказываются более реальными, чем люди во плоти и с безупречным пульсом."},{"story_id":162,"story":"Совесть – критерий искренности твоего эгоизма. Внимательно прислушивайся к ней."},{"story_id":163,"story":"Истина, которую ты выражаешь, не имеет ни прошлого, ни будущего. Она есть, и ей этого достаточно."},{"story_id":164,"story":"Судьба не толкает тебя туда, куда ты не хочешь идти. Выбираешь только ты сам. Ты сам выбираешь свою судьбу."},{"story_id":165,"story":"Ты – создание света. Из света ты пришел и в свет уйдешь. И на каждом твоем шагу тебя окружает свет твоего бесконечного бытия."},{"story_id":166,"story":"Вообрази Вселенную – прекрасную, справедливую и совершенную. И будь уверен в одной вещи: Бытие уже вообразило ее. Только она немного лучше, чем ты можешь себе представить."},{"story_id":167,"story":"Поступай с другими так, как ты действительно хотел бы поступать с другими."},{"story_id":168,"story":"То, что ты видишь, - это твое собственное сознание. Если поднять этот занавес – как же изменится сцена!"},{"story_id":169,"story":"Вот тебе тест, с помощью которого можно определить, закончена ли твоя миссия на земле: Если ты жив – нет"},{"story_id":170,"story":"Избегай проблем, и тебе никогда не придется решать их."},{"story_id":171,"story":"Не бойтесь расставаний. Прощаться необходимо, чтобы можно было встретиться снова. Пройдет время, и новая встреча состоится обязательно, если вы – друзья."},{"story_id":172,"story":"У тебя достаточно силы, чтобы сделать все, чего ты пожелаешь, за двумя исключениями: ты не можешь создать реальность; ты не можешь разрушить ее."},{"story_id":173,"story":"Когда же ты научишься, быть готовым к тому, что то, чего ты не можешь даже вообразить, когда нибудь случится?"},{"story_id":174,"story":"Тебе никогда не было дано желание, вместе с которым не дана была бы сила для его осуществления. Правда, ради этого приходится еще и потрудиться."},{"story_id":175,"story":"Что делать с дарами, которые ты несешь другим, решать им, а не тебе."},{"story_id":176,"story":"Не обязательно знать, что ждет впереди. Тебя направляет твоя высшая истина, и куда бы она ни привела, это и будет то, что тебе нужно."},{"story_id":177,"story":"Понимание приходит к тебе со скоростью света, но не спеши формулировать его даже для самого себя – это стоит делать только шаг за шагом. Иногда самое разумное – помалкивать о том, что знаешь."},{"story_id":178,"story":"Твоя религия – это твой способ поиска истины."},{"story_id":179,"story":"Единственное, что имеет значение, когда заканчивается твой земной срок: Умел ли ты любить? Каким было качество твоей любви?"},{"story_id":180,"story":"Ошибок не бывает. События, которые ты призываешь к себе, как бы они ни были неприятны, необходимы для того, чтобы ты мог узнать то, что узнать должен. Любой твой шаг необходим для того, чтобы достичь места, которое ты выбрал достичь."},{"story_id":181,"story":"Ты отдал жизнь ради того, Чтобы стать именно тем, кем являешься сейчас. Стоило ли?"},{"story_id":182,"story":"Жизнь ЕСТЬ. Это такое место, куда пришел ради исцеления, удовольствия, энергии, перспективы."},{"story_id":183,"story":"Один из способов выбрать будущее – просто поверить в то, что оно неизбежно."},{"story_id":184,"story":"Давать имена вещам – дело вполне безобидное. Давать имена идеям – значит создавать религию. И ты посмеешь?"},{"story_id":185,"story":"Ты всегда оставался верен своему чувству истины, собственной внутренней этике, даже когда это было трудно или опасно или когда другие называли тебя чудаком. Эта странность выделяет тебя среди других и обрекает на одиночество. Что ты думаешь с этим делать?"},{"story_id":186,"story":"Твое Есьм не нуждается в том, чтобы ты кому-либо рассказывал, как оно работает."},{"story_id":187,"story":"Глубина твоей веры в несправедливость и трагедию – это знак твоего невежества. То, что гусеница считает концом света, мастер называет бабочкой."},{"story_id":188,"story":"Все вы Свободны. Делать. Все. Что вам угодно. Делать."},{"story_id":189,"story":"Для того, чтобы жить свободно и счастливо, необходимо лишь пожертвовать скукой. Поверь, иногда это не такая уж легкая жертва."},{"story_id":190,"story":"То, что тебя вдохновляет, является для тебя также и путеводной нитью, и защитой."},{"story_id":191,"story":"Знай, что рядом с тобой всегда находится реальность любви, и в любую минуту ты в состоянии преобразить собственный мир с помощью нового знания."},{"story_id":192,"story":"В любви вы едины – это реальность, а миражи изменить реальность не могут. Не забывайте. Что бы там ни мерещилось."},{"story_id":193,"story":"Поверь, что ты знаешь все ответы,- и ты знаешь все ответы. Поверь, что ты мастер,- и ты – мастер."},{"story_id":194,"story":"Ни один человек не может решить проблемы другого человека, чья проблема заключается в том, что он не хочет, чтобы его проблемы были решены."},{"story_id":195,"story":"Ты не должен бороться за то, чтобы жить так, как ты хочешь. Живи так, как ты хочешь, и плати за это требуемую цену, какой бы она ни была."},{"story_id":196,"story":"Ты не создаешь собственную реальность. Ты создаешь собственные иллюзии. Большая разница!"},{"story_id":197,"story":"Любая идея, которая требует восхищения, нереальна. Любой Бог, который требует поклонения, не существует."},{"story_id":198,"story":"Кто ты? Ты – тот, что попросился на Землю ради того, чтобы сделать здесь что-то замечательное, что-то для тебя очень важное, что-то такое, чего нельзя сделать больше нигде и никогда."},{"story_id":199,"story":"Иногда единственный путь к победе – сдаться."},{"story_id":200,"story":"Жертва – это отказ от чего-то желанного ради чего-то нежеланного. Она никогда не бывает нужна."},{"story_id":201,"story":"Когда будешь умирать, отнесись к этому с определенной серьезностью. Радость на смертном пути – этого обычно не понимают менее развитые формы жизни. Они сочтут тебя сумасшедшим."},{"story_id":202,"story":"В этой книге все может оказаться ошибкой."},{"story_id":203,"story":"Облакам не страшно упасть в море, ведь они (а) не могут упасть и (б) не могут утонуть. Впрочем, никто не мешает им верить, что с ними такое может случиться. И они могут бояться сколько угодно, если захотят."}]

export default stories