import * as React from "react";
import { motion } from "framer-motion";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

const variants = {
  open: {
    y: 0,
    opacity: 1,
    transition: {
      y: { stiffness: 1000, velocity: -100 }
    }
  },
  closed: {
    y: 50,
    opacity: 0,
    transition: {
      y: { stiffness: 1000 }
    }
  }
};

const menus = [
  ["/","Справочник\u00A0мессии"], 
  ["/foreword","Предисловие"]  
];

export const MenuItem = ({ i }) => {
  
  const menu = menus[i];
  return (
    <motion.li
      variants={variants}
      whileHover={{ scale: 1.1 }}
      whileTap={{ scale: 0.95 }}
    >
      <Link to={menu[0]}>
        {menu[1]}
      </Link>     
    </motion.li>
  );
};
